// image format for retrieving custom attributes

const BaseImageFormat = Quill.import('formats/image');
const ImageFormatAttributesList = ['alt', 'height', 'width', 'style'];

export class ImageFormat extends BaseImageFormat {
  static formats(domNode: any) {
    return ImageFormatAttributesList.reduce(function (formats: any, attribute) {
      if (domNode.hasAttribute(attribute)) {
        formats[attribute] = domNode.getAttribute(attribute);
      }
      return formats;
    }, {});
  }

  format(name: string, value: any) {
    if (ImageFormatAttributesList.includes(name)) {
      if (value) {
        this.domNode.setAttribute(name, value);
      } else {
        this.domNode.removeAttribute(name);
      }
    } else {
      super.format(name, value);
    }
  }
}

@Injectable({
  providedIn: 'root',
})
export class SharedService {
  static PRICING_PLANS: any;

  static PRICING_PLANS_ARRAY: Array<PricingPlan> = [];

  static isQuillEditorOpen: boolean;

  isReady: EventEmitter<boolean> = new EventEmitter();

  hideFooter: EventEmitter<boolean> = new EventEmitter();

  hideHeader: EventEmitter<boolean> = new EventEmitter();

  hideEntireFooter: EventEmitter<boolean> = new EventEmitter();

  keyPressed: EventEmitter<any> = new EventEmitter();

  isMail: EventEmitter<boolean> = new EventEmitter();

  isExternalPage: EventEmitter<boolean> = new EventEmitter();

  private paymentFailureModalRef: NgbModalRef;

  username: string;

  //
  constructor(
    private http: HttpClient,
    private modalService: NgbModal,
    private notificationService: NotificationService,
    private store: Store<AppState>,
  ) {
    /**
     * Get user's settings
     */
    this.store
      .select(state => state.user)
      .subscribe((user: UserState) => {
        this.username = user.username;
      });
  }

  sortByDate(data: any[], sortField: string): any[] {
    return data.sort((a: any, b: any) => new Date(b[sortField]).getTime() - new Date(a[sortField]).getTime());
  }

  getHashPurePasswordWithUserName(password: string): string {
    const username = this.username.toLowerCase();
    const salt = this.createSalt('$2a$10$', username);
    return bcrypt.hashSync(password, salt);
  }

  private createSalt(salt: string, username: string): any {
    username = username.replace(/[^ A-Za-z]/g, '');
    username = username || 'test';
    if (salt.length < 29) {
      return this.createSalt(salt + username, username);
    }
    return salt.slice(0, 29);
  }

  /**
   * @description
   * Prime Users - Can create as many folders as they want
   */
  openCreateFolderDialog(isPrime: boolean, customFolders: Folder[], callback: { self: any; method: string } = null) {
    this.openModal(callback);
  }

  private openModal(callback: { self: any; method: string } = null) {
    const modal: NgbModalRef = this.modalService.open(CreateFolderComponent, {
      centered: true,
      windowClass: 'modal-sm mailbox-modal create-folder-modal',
    });
    (<CreateFolderComponent>modal.componentInstance).callback = callback;
  }

  loadPricingPlans() {
    if (SharedService.PRICING_PLANS) {
      return;
    }
    this.http.get('./assets/static/pricing-plans.json').subscribe((data: any) => {
      SharedService.PRICING_PLANS = data;
      SharedService.PRICING_PLANS_ARRAY = [];
      Object.keys(data).forEach(key => {
        if (key !== PlanType.PARAGON) {
          data[key].name = key;
          SharedService.PRICING_PLANS_ARRAY.push(data[key]);
        }
      });
    });
  }

  showPaymentFailureDialog() {
    if (!this.paymentFailureModalRef) {
      this.paymentFailureModalRef = this.modalService.open(PaymentFailureNoticeComponent, {
        centered: true,
        windowClass: 'modal-sm',
        backdrop: 'static',
        keyboard: false,
      });
      this.paymentFailureModalRef.result.then(() => (this.paymentFailureModalRef = null));
    }
  }

  copyToClipboard(value: string) {
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = value;
    document.body.append(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    selBox.remove();
    this.notificationService.showSnackBar('Copied to clipboard successfully.');
  }

  base64ToUint8Array(base64Data: string): Uint8Array {
    const decodedData: any = atob(base64Data);
    const { length } = decodedData;
    const uint8Array = new Uint8Array(length);
    for (let i = 0; i < length; i++) {
      uint8Array[i] = decodedData.charCodeAt(i);
    }
    return uint8Array;
  }

  downloadFile(file: File) {
    const a = document.createElement('a');
    a.style.display = 'none';
    const url = window.URL.createObjectURL(file);
    a.href = url;
    a.download = file.name;
    document.body.append(a);
    a.click();
    window.URL.revokeObjectURL(url);
    a.remove();
  }

  arrayBufferToBase64(buffer: ArrayBuffer) {
    let binary = '';
    const bytes = new Uint8Array(buffer);
    const length = bytes.byteLength;
    for (let i = 0; i < length; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
  }

  /**
   * Parsing UsersKeys is very annoying now due to unstructured BACKEND response for several user types
   * Parsing UsersKeys with given email and RETURN
   * @param usersKeys
   * @param email
   */
  parseUserKey(usersKeys: Map<string, GlobalPublicKey>, email: string): any {
    const parsedData = parseEmail.parseOneAddress(email) as parseEmail.ParsedMailbox;
    if (!parsedData) {
      return {
        isCTemplarKey: false,
        isExistKey: false,
        keys: [],
      };
    }
    const parsedEmail = parsedData.address;
    let isCTemplarKey = false;
    let isExistKey = false;
    let keys = [];
    if (
      usersKeys.has(parsedEmail) &&
      !usersKeys.get(parsedEmail).isFetching &&
      usersKeys.get(parsedEmail).key &&
      usersKeys.get(parsedEmail).key.length > 0
    ) {
      const keyObject: any = usersKeys.get(parsedEmail).key[0];
      // Check if it has keys, but STILL not sure if it is CTemplar user or NOT
      if (keyObject.exists) {
        isExistKey = true;
      }
      // Check if it is CTemplar user - checking with `is_enabled` flag,
      // TODO - Should be improved to check with `is_enabled` flag
      if (keyObject.internal || keyObject.local) {
        isCTemplarKey = true;
      }
      if (isExistKey) {
        keys = keyObject.key;
      }
    }
    return {
      isCTemplarKey,
      isExistKey,
      keys,
    };
  }

  isRFCStandardValidEmail(address: string): boolean {
    return !!parseEmail.parseOneAddress({ input: address, rejectTLD: true });
  }
}

export function sortByString(data: any[], field: string) {
  return data.sort((a, b) => {
    if (a[field] < b[field]) {
      return -1;
    }
    if (a[field] > b[field]) {
      return 1;
    }
    return 0;
  });
}

export function isComposeEditorOpen(): boolean {
  return (
    SharedService.isQuillEditorOpen ||
    document.querySelectorAll('app-compose-mail-dialog').length > 0 ||
    document.querySelectorAll('app-compose-mail').length > 0 ||
    document.querySelectorAll('ngb-modal-window').length > 0
  );
}

export function getCryptoRandom(): number {
  let array = new Uint32Array(1),
    max = Math.pow(2, 32),
    randomValue = window.crypto.getRandomValues(array)[0] / max;
  return randomValue;
}

